.search-con {
  width: 100%;
  height: 60px;
  color: #fff;
  line-height: 60px;
  margin-top: 40px;
}

.search-con-wrap {
  width: 700px;
  height: 60px;
  box-sizing: border-box;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  overflow: hidden;
}

.search-con-wrap-left {
  flex: 1;
  height: 60px;
  background-color: #fff;
  box-sizing: border-box;
  padding: 10px 20px 10px 20px;
  line-height: 60px;
  display: flex;
  display: -webkit-flex;
  align-items: center;
}

.search-con-wrap-left-search {
  width: 400px;
  height: 30px;
  position: relative;
  margin-left: 20px;
}

.search-con-wrap-left-search input {
  border: none;
  outline: none;
  font-size: 16px;
  width: 95%;
  height: 28px;
  line-height: 28px;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 0;
  top: 0;
}

.search-icon {
  font-size: 20px;
}

.search-con-wrap-right {
  width: 150px;
  height: 60px;
  background-color: rgba(205, 0, 51, 0.8);
  text-align: center;
  cursor: pointer;
}

.search-con-wrap-right:hover {
  background-color: rgba(205, 0, 51, 1);
}
