.card {
  width: 270px;
  height: 325px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 1px 2px rgba(0,0,0,0.15);
  margin: 0 15px 20px;
}

.card-top {
  width: 100%;
  height: 175px;
  background-color: #60c;
  position: relative;
}

.card-top-img {
  position: absolute;
  left: 16px;
  right: 16px;
  top: 16px;
  bottom: 16px;
  z-index: 1;
}

.card-top-img img {
  display: block;
  width: 100%;
  height: 100%;
  transition: all 1s;
  transform: scale(1);
  border-radius: 5px;
}

.card:hover .card-top .card-top-img img {
  transform: scale(1.06);
}

.card-body {
  width: 100%;
  height: 115px;
  box-sizing: border-box;
  padding-top: 10px;
}

.card-body-title {
  width: 100%;
  font-size: 14px;
  box-sizing: border-box;
  padding-left: 10px;
  height: 20px;
}

.card-body-title a {
  display: inline-block;
  width: 100%;
  height: 20px;
  line-height: 20px;
  color: #09f;
  cursor: pointer;
}

.card-body-title a:hover {
  text-decoration: underline;
}

.card-body-desc {
  font-size: 12px;
  color: #666;
  line-height: 16px;
  margin-top: 5px;
  box-sizing: border-box;
  padding-left: 10px;
  max-height: 40px;
  height: 40px;
}

.card-body-tag {
  width: 100%;
  height: 35px;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  box-sizing: border-box;
  padding-left: 10px;
}

.card-body-tag-icon {
  width: 20px;
  height: 20px;
}

.card-body-tag-icon i {
  font-size: 14px;
  color: #888;
}

.card-body-tag-items {
  font-size: 14px;
  color: #888;
  padding-left: 10px;
}

.card-bottom {
  width: 100%;
  height: 35px;
  background-color: #eee;
}