@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?a6tvcw');
  src:  url('fonts/icomoon.eot?a6tvcw#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?a6tvcw') format('truetype'),
    url('fonts/icomoon.woff?a6tvcw') format('woff'),
    url('fonts/icomoon.svg?a6tvcw#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icomoon-"], [class*=" icomoon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon-search:before {
  content: "\e986";
}
.icomoon-list:before {
  content: "\e9bb";
}
.icomoon-eye:before {
  content: "\e9ce";
}
.icomoon-heart:before {
  content: "\e9da";
}
