.header-con {
  width: 100%;
  height: 400px;
  background-color: #000;
  position: relative;
  overflow: hidden;
}

.header-con-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  z-index: 0;
}

.header-con-items {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
}

.header-con-items-title {
  width: 100%;
  height: 40px;
  font-size: 36px;
  color: #fff;
  line-height: 40px;
  text-align: center;
}