html, body {
  width: 100%;
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100%;
  min-height: 100%;
  background-color: #eee;
}

@font-face {
  font-family: 'icomoon';
  src:  url(/static/media/icomoon.94e7af2f.eot);
  src:  url(/static/media/icomoon.94e7af2f.eot#iefix) format('embedded-opentype'),
    url(/static/media/icomoon.5039f9ac.ttf) format('truetype'),
    url(/static/media/icomoon.42f5c529.woff) format('woff'),
    url(/static/media/icomoon.c7e7d6d8.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icomoon-"], [class*=" icomoon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon-search:before {
  content: "\E986";
}
.icomoon-list:before {
  content: "\E9BB";
}
.icomoon-eye:before {
  content: "\E9CE";
}
.icomoon-heart:before {
  content: "\E9DA";
}

.App {
  width: 100%;
  min-height: 100%;
}

.home {
  width: 100%;
  min-height: 100vh;
}
.search-con {
  width: 100%;
  height: 60px;
  color: #fff;
  line-height: 60px;
  margin-top: 40px;
}

.search-con-wrap {
  width: 700px;
  height: 60px;
  box-sizing: border-box;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  overflow: hidden;
}

.search-con-wrap-left {
  flex: 1 1;
  height: 60px;
  background-color: #fff;
  box-sizing: border-box;
  padding: 10px 20px 10px 20px;
  line-height: 60px;
  display: flex;
  display: -webkit-flex;
  align-items: center;
}

.search-con-wrap-left-search {
  width: 400px;
  height: 30px;
  position: relative;
  margin-left: 20px;
}

.search-con-wrap-left-search input {
  border: none;
  outline: none;
  font-size: 16px;
  width: 95%;
  height: 28px;
  line-height: 28px;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 0;
  top: 0;
}

.search-icon {
  font-size: 20px;
}

.search-con-wrap-right {
  width: 150px;
  height: 60px;
  background-color: rgba(205, 0, 51, 0.8);
  text-align: center;
  cursor: pointer;
}

.search-con-wrap-right:hover {
  background-color: rgba(205, 0, 51, 1);
}

.header-con {
  width: 100%;
  height: 400px;
  background-color: #000;
  position: relative;
  overflow: hidden;
}

.header-con-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  z-index: 0;
}

.header-con-items {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
}

.header-con-items-title {
  width: 100%;
  height: 40px;
  font-size: 36px;
  color: #fff;
  line-height: 40px;
  text-align: center;
}
.card {
  width: 270px;
  height: 325px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 1px 2px rgba(0,0,0,0.15);
  margin: 0 15px 20px;
}

.card-top {
  width: 100%;
  height: 175px;
  background-color: #60c;
  position: relative;
}

.card-top-img {
  position: absolute;
  left: 16px;
  right: 16px;
  top: 16px;
  bottom: 16px;
  z-index: 1;
}

.card-top-img img {
  display: block;
  width: 100%;
  height: 100%;
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-transform: scale(1);
          transform: scale(1);
  border-radius: 5px;
}

.card:hover .card-top .card-top-img img {
  -webkit-transform: scale(1.06);
          transform: scale(1.06);
}

.card-body {
  width: 100%;
  height: 115px;
  box-sizing: border-box;
  padding-top: 10px;
}

.card-body-title {
  width: 100%;
  font-size: 14px;
  box-sizing: border-box;
  padding-left: 10px;
  height: 20px;
}

.card-body-title a {
  display: inline-block;
  width: 100%;
  height: 20px;
  line-height: 20px;
  color: #09f;
  cursor: pointer;
}

.card-body-title a:hover {
  text-decoration: underline;
}

.card-body-desc {
  font-size: 12px;
  color: #666;
  line-height: 16px;
  margin-top: 5px;
  box-sizing: border-box;
  padding-left: 10px;
  max-height: 40px;
  height: 40px;
}

.card-body-tag {
  width: 100%;
  height: 35px;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  box-sizing: border-box;
  padding-left: 10px;
}

.card-body-tag-icon {
  width: 20px;
  height: 20px;
}

.card-body-tag-icon i {
  font-size: 14px;
  color: #888;
}

.card-body-tag-items {
  font-size: 14px;
  color: #888;
  padding-left: 10px;
}

.card-bottom {
  width: 100%;
  height: 35px;
  background-color: #eee;
}
.body-con {
  width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 20px;
  overflow: hidden;
}

.body-con-items {
  width: 100%;
  display: flex;
  display: -webkit-flex;
  justify-content: left;
  flex-wrap: wrap;
}
